<i18n>
{
  "en": {
    "SuccessfulPayment": "Successful payment",
    "SuccessfullyReturned": "Payment success",
    "Consumption": "Consumption",
    "Unit": "(Yuan)",
    "cost": "{count}Yuan",
    "TheLeaseTime": "The lease time",
    "time": "{0}days, {1}hours, {2}minutes.",
    "ShopAddress": "Shop address",
    "address": "1 floor, building 3, garden district.",
    "Complete": "Complete"
  },
  "zh": {
    "SuccessfulPayment": "成功支付",
    "SuccessfullyReturned": "支付成功",
    "Consumption": "消费",
    "Unit": "（元）",
    "cost": "{count}元",
    "TheLeaseTime": "租借时长",
    "time": "{0}天{1}小时{2}分钟",
    "ShopAddress": "店铺位置",
    "address": "花园小区3号楼1层",
    "Complete": "完成"
  }
}
</i18n>

<template>
  <div class="restituteSuccessfully">
    <head-top :title='$t("SuccessfulPayment")' :custom-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <img src="../../assets/scanning_images/succeed.png" alt="">
      <span>{{ $t("SuccessfullyReturned") }}</span>
      <ul class="list">
        <li>
          <span>{{ $t("Consumption") }}<b>{{ $t("Unit") }}</b></span>
          <strong>{{ $t("cost",{count: data.fee / 100}) }}</strong>
        </li>
        <li>
          <span>{{ $t("TheLeaseTime") }}</span>
          <strong>{{ $t("time", timers) }}</strong>
        </li>
        <li>
          <span>{{ $t("ShopAddress") }}</span>
          <strong>{{ data.lent_adress }}</strong>
        </li>
      </ul>
      <button @click="goHandle" class="btn">{{ $t("Complete") }}</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
export default {
  name: 'restituteSuccessfully',
  data () {
    return {
      data: {
        fee: 0
      }, // 订单信息
      timers: [0, 0, 0]
    }
  },

  components: {
    headTop
  },

  mounted () {
    this.data = this.$route.params.data
    // 计算租借时长
    this.getTimes()
  },

  methods: {
    ...mapActions([
      'sidebarSwitch'
    ]),
    goBackHander () {
      console.log(11)
      this.sidebarSwitch(false)
      this.$router.go(-3)
    },
    goHandle () {
      // this.dataInteractionRequest({
      //   msgType: '0x5221',
      //   type: 3,
      //   page: 1,
      //   count: 10
      // })
      this.sidebarSwitch(false)
      this.$router.go(-3)
    },
    getTimes () {
      let d, h, m
      let ldate = new Date(this.data.lent_time)
      console.log(ldate)
      let Rdate = new Date(this.data.return_time)
      console.log(Rdate)
      console.log(this.data)
      let times = Rdate.getTime() - ldate.getTime()
      // console.log(times)
      d = parseInt(times / 1000 / 3600 / 24)
      times = times - d * 1000 * 3600 * 24
      h = parseInt(times / 1000 / 3600)
      times = times - h * 1000 * 3600
      m = parseInt(times / 1000 / 60)
      this.timers = [d, h, m]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.restituteSuccessfully{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    img{
      height: 185px;
      margin: 110px auto 70px auto;
    }
    span{
      font-size: 30px;
      display: block;
    }
    .list{
      .wh(100%, 270px);
      padding: 0 4%;
      margin-top: 8%;
      li{
        .wh-l(100%, 90px);
        span{
          .wh-f(30%, 100%);
          text-align: left;
          font-size: 30px;
          b{
            font-size: 26px;
          }
        }
        strong{
          .wh-f(70%, 100%);
          font-size: 26px;
          text-align: right;
          color: #999;
        }
      }
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      color: #5A595A;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      // box-shadow: 0px 5px 10px rgba(244, 218, 33, 0.6)
      box-shadow: 0px 4px 25px@themeColor
    }
  }
}
</style>



